.login {
  width: 100%;
  height: 100%;
  background-color: #1da9cc;
}
.login .login-header {
  display: flex;
  align-items: center;
  width: 90%;
  height: 60px;
  margin: auto;
  border-bottom: #000 solid 1px;
}
.login .login-header img {
  height: 24px;
  margin: 0 20px;
}
.login .login-header h1 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 0;
  width: 80%;
}
.login .login-content {
  width: 360px;
  height: 240px;
  background-color: #fff;
  margin: 200px auto 0;
  padding: 0 20px;
}
.login .login-content h2 {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  padding: 20px 0 0;
}
.login .login-content .login-form-button {
  width: 100%;
}
